body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-logo {
  top: 0;
  height: 70px;
  width: auto;
}

.nav-callnow {
  height: 30px !important;
  top: 25px !important;
  right: 25px !important;
  margin: 0 !important;
  padding: 0 !important;
  position: absolute;
}

@media (max-width: 700px) {
  .nav-callnow {
    top: 0px !important;
    position: relative;
    left: 0px;
  }
}

.home-images {
  height: 300px !important;
  width: auto;
}

@media (max-width: 600px) {
  .home-images {
    height: 200px !important;
    width: auto;
  }
}

li {
  list-style-type: none;
}
.about-picture {
  height: 600px !important;
  width: auto !important;
}
.image {
  image-resolution: 300dpi;
  border-radius: 8px;
  object-position: center;
}

.card-about,
.image {
  height: 277px !important;
  width: 180px !important;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0);
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: 0.5s ease;
}

.card:hover .overlay {
  height: 100%;
}

.image,
.overlay {
  border-radius: 8px;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  bottom: 1%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.text-link {
  padding: 0;
  border: none;
  background: none;
  color: white;
  font-size: 12px;
  bottom: 0% !important;
  left: 50% !important;
  transform: translate(-50%, -50%);
  text-align: center;
}


.image {
  display: block;
  width: 100%;
  height: auto;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.1);
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: 0.5s ease;
}

.text {
  color: white;
  font-size: 20px;
  position: absolute;
  bottom: 1%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

